/* ====================================================== 
   <!-- Navigation -->                                    
/* ====================================================== */


.app-topnav__wrapper {
    position: fixed;
    z-index: 101;
    right: 35px;
    top: 35px;
    font-family: 'Conv_Gotham-Medium';
    font-size: 0.75rem;
    color: #fff;
    display: flex;
    align-items: center;
    transition: .5s linear; /* The value should not be too small */

    
    &.scene-start {
        opacity: 0;
    } 
    &.scene-end {
        opacity: 0;
        animation: uix-cssAnim--navShow 0.5s linear forwards;
        animation-delay: 1s;
    } 
    
    
    
    > span {
        display: inline-block;
        padding: 0 .4rem;
        font-weight: bold;
        transition: .2s ease-in-out;
    }
    
    
    > a {
        margin: 0;
        width: 35px;
        height: 35px;
        font-size: 0;
        text-indent: -9999px;
        display: inline-block;
        appearance: none;
        box-shadow: none;
        border-radius: 100%;
        border: 1px solid rgba(255,255,255,1);
        cursor: pointer;
        overflow: hidden;
        transition: .2s ease-in-out;

        .app-topnav__bars {
            height: 1px;
            width: 45%;
            background: rgba(255,255,255,1);
            display: block;
            margin: 0 auto 3px;
            
            
            &.app-topnav__bars--top {
              margin-top: 12px;
              left: 3px;
              position: relative;
            }
            &.app-topnav__bars--middle {
              width: 43%;
              position: relative;
            }
            &.app-topnav__bars--bottom {
              margin-bottom: 0;
              left: -2px;
              position: relative;
            }
    
        }
        
    }
    
    &:hover {
        > a {
            transform: scale(1.1);
        }

        > span {
            opacity: 0;
        }
    }
    

    
    
}


@keyframes uix-cssAnim--navShow {
	0%   { 

        opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


@media all and (max-width: 768px) {
	
	.app-topnav__wrapper {
        > span {
            display: none;
        }
	}

}
