/* ====================================================== 
   <!-- Brand -->                         
/* ====================================================== */
.uix-brand {
	float: left;
	font-size: 1.3125rem;
	font-weight: 700;
	text-transform: uppercase;
	white-space: nowrap;
	max-width: 95%;
	overflow: hidden;
	text-overflow: ellipsis;
    position: relative;
    transition: 0.6s ease-in-out;
    pointer-events: none;
    user-select: none;
    opacity: 0;
    
    /* ready*/
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 11;
    position: fixed;
    
    &.scene-ready {
        opacity: 1;
    } 
    &.scene-start {
        opacity: 0;
    } 
    &.scene-end {
        position: relative;
        top: auto;
        left: auto;
        transform: none;
        opacity: 0;
        animation: uix-cssAnim--brandShow 0.5s linear forwards;
        animation-delay: 1s;
        
        svg {
            width: 85px;
            margin: 30px;
        }
    } 
    
	
	img {
		max-height: 50px;
		margin-top: 0.75rem;
	}
	

	.uix-brand__description {
		padding: 0;
		@include ellipsis();
		width: 90%;
		line-height: 1.2;
		font-weight: 100;
		font-size: 0.75rem;
	}

    
    svg .cls-1{
        fill:#fff;
    } 
    svg { 
        width: 100px; 
        margin: 1rem 0;
    }

	
}

@keyframes uix-cssAnim--brandShow {
	0%   { 

        opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


.uix-brand--mobile {
    display: none;
}

@media all and (max-width: 768px) {

	.uix-brand--mobile {
		display: block;
	    text-align: center;
		
	    img {
			max-width: 90%;
			max-height: 100px;
			margin: 20px auto;
	    }	
		
	}
	
	.uix-brand img {
		margin-bottom: 0.75rem;
	}
	
}