/* 
 ---------------------------
 1. Cursor
 ---------------------------
 */ 
* {
    //cursor: none;
}

    
.app-cursor {
    position: absolute;
    width: 38px;
    height: 38px;
    top: 0%;
    left: 0%;
    border-radius: 50%;
    backface-visibility: hidden;
    transition: transform 0.1s ease-out;
    z-index: 50;
    margin: -20px 0 0 -20px;
    pointer-events: none;
    transform-origin: center center;


    
    
    .app-cursor__progress {


        /* progress */
        svg.dot {
            transition: transform 0.3s ease-out;
            position: absolute;
            width: 42px;
            height: 42px;
            top: 0;
            left: 0;
            margin: -2px 0 0 -2px;
			z-index: 2;

            &.is-disabled {
                transform: scale(0);
            }


            circle {
                r: 18px;
                cx: 21px;
                cy: 21px;
                opacity: 0;
                fill: transparent;
            }


        }

        /* progress of back */
        svg.dot-back {
            transition: transform 0.3s ease-out;
            position: absolute;
            width: 42px;
            height: 42px;
            top: 0;
            left: 0;
            margin: -2px 0 0 -2px;
			z-index: 2;

            &.is-disabled {
                opacity: 0;
            }


            circle {
                r: 18px;
                cx: 21px;
                cy: 21px;
                fill: transparent;
            }


        }

		

        /* main */  
        svg.circle {
            transition: transform 0.3s ease-out;
            position: absolute;
            width: 38px;
            height: 38px;
            top: 0;
            left: 0;
            opacity: 1;

            &.is-disabled {
                transform: scale(0);
            }
            path {
                stroke: #fff;
                fill: transparent;
                stroke-width: 2px;

            }


        }







        /* enter arrow */
        svg.arrow {
            transition: transform 0.3s ease-out;
            position: absolute;
            width: 38px;
            height: 38px;
            top: 0;
            left: 0;
            transform-origin: center center;
            transform: rotate(-90deg) scale(0) translateY(0);

            &.is-active  {
                transform: rotate(-90deg) scale(1) translateY(0);
            } 

            &.is-down  {
                transition: transform 1s cubic-bezier(0.77,0.2,0.05,1.0);
                transform: rotate(-90deg) scale(0) translateX(500px);
            } 

            path {
                stroke: #fff;
                fill: #fff;
                stroke-width: 0px;
            }    
        }



        /* back arrow */
        svg.back {
            transition: transform 0.3s ease-out;
            position: absolute;
            width: 38px;
            height: 38px;
            top: 0;
            left: 0;
            transform-origin: center center;
            transform: rotate(180deg) scale(0) translateY(0);

            &.is-active  {
                transform: rotate(180deg) scale(0.5) translateY(0);
            } 

            &.is-down  {
                transition: transform 1s cubic-bezier(0.77,0.2,0.05,1.0);
                transform: rotate(180deg) scale(0) translateX(500px);
            }  
			
            path {
                stroke: #fff;
                fill: #fff;
                stroke-width: 0px;
            }

        }  

    }
    
    
    /* cursor interaction */
    &:not(.is-backbtn).is-down {
        transform: scale(0.7) !important;
    }

    &.is-moving {
        opacity: .8;
    }

    &.is-hover {
//        mix-blend-mode: normal;
//        backdrop-filter: blur(24px) contrast(1.5) brightness(1.25) saturate(1.5);
        transform: scale(1.4);
       

        svg.circle {
            transform: scale(0.01);
        }
		
		
        /* back arrow */
        svg.back.is-active {
            transform: rotate(-90deg) scale(0.5) translateY(0) !important;
		}
		
		svg.back.is-down  {
			transform: rotate(-90deg) scale(0) translateX(500px);
		}  
		
		

    }   
	
    &.is-down {
		svg.back.is-down  {
			transform: rotate(180deg) scale(0) translateX(500px);
		}  
    }
	
    
    
}



    

/* 
 ---------------------------
 2. Navigation
 ---------------------------
 */ 
               
.app-menu {
    position: absolute;
    top: -9999rem;
    left: -9999rem;
    opacity: 0;
    z-index: 1;
    margin-left: 0;
    margin-top: 0;
    padding: 1em;
    width: 200px;
    color: #fff;
    background: rgba(255, 255, 255, 0.2);
    border-radius: .5em;
    font-size: 12px;
    line-height: 1.2;
    transition: .2s linear;
    pointer-events: none;
    user-select: none;
    
    
    &.is-active {

    }

    &.is-hover {
        opacity: 1;
        margin-top: -5px; 
    }       



     h3 {
        font-family: $font-desc;
        font-size: 1rem;
        color: #fff;
        margin: 0;
        padding: 0;
        padding-bottom: .5rem;
    }  

     p {
        margin: 0;
        font-family: $font-desc;
    }

    &::before {
        content: '';
        position: absolute;
        top: -10px;
        left: -10px;
        width: 10px;
        height: 10px;
        border: 2px solid #fff;
        border-radius: 50%;
        font-size: 0.75rem;
