/* 
 ---------------------------
 3. Loader
 ---------------------------
 */ 

.app-loader-progress {
    transition: .2s linear;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 10;
    font-weight: normal;
    margin-left: 0;
    opacity: .8;
    pointer-events: none;
    user-select: none; 
    
    span,
    strong { 
        font-weight: normal;
        font-size: 14px;
        font-family: 'Conv_almonte';
        color: #01152e;
        background: linear-gradient(to bottom, #001483 0%,#01152e 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        text-transform: uppercase;
    }




    .app-loader-progress__counter {
        em {
            font-style: normal;
            font-size: 14px;
            font-family: 'Conv_almonte';
            color: #01152e;
            text-transform: lowercase;
            color: #fff;
            font-weight: 300;
        }        

    } 
    
}   

.app-loader-headphone {
    transition: .2s linear;
    position: fixed;
    right: 20px;
    bottom: 20px;
    z-index: 10;
    font-weight: normal;
    opacity: .4;
    pointer-events: none;
    user-select: none; 
    
    &.is-hide {
        opacity: 0;
    }
    
    svg {
        width: 20px;
        height: 20px;
        path {
            fill: #fff;
        }
    }   

    
}     



/* 
 ---------------------------
 4. Screen - Home
 ---------------------------
 */ 

#app-screen-home {
    position: fixed;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    margin-top: -20vh;
    height: 15rem;
    display: none;
    width: 850px;
    transition: .2s linear;
    pointer-events: none;
    user-select: none;
    
    &.is-click {
        transform: translate(-50%,-50%) scale(1.2);
        filter: blur(5px);
        opacity: 0;
    }

    
    .app-screen-home__desc {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;

    }
    
    h2 {
        text-align: center;
        font-size: 4.2rem;
        letter-spacing: 1.2rem;
        color: #fff;
        font-family: 'Conv_FjallaOne-Regular';
        font-weight: normal;
        position: relative;
        text-transform: uppercase;

        > div:nth-child(3)::after {
            content: '';
            display: block;
            top: -20%;
            position: absolute;
            left: 30%;
            background: #000a19;
            height: 120%;
            width: 10px;
            transform: rotate(45deg);
            transform-origin: center center;
            z-index: 1;
            height: 0;
            transition: .2s linear;
            transition-delay: 2s;

        } 

    }


    p {
        font-size: 0.8125rem;
        text-transform: uppercase;
        text-align: center;
        letter-spacing: 0.3rem;
        color: #fff;
        font-weight: normal;
        opacity: .8;
    } 


    &.is-active {
        display: block;

        h2 > div:nth-child(3)::after {
            height: 120%;
        }  
    }   

    
}


@media all and (max-width: 768px) {
	
	#app-screen-home {
		margin-top: 0;
        transform: translate(-50%,-50%) scale(.4);
	}

}




