/* 
 ---------------------------
 6. Button
 ---------------------------
 */  
    
.app-enter-btn {
    transition: .2s linear;
    font-size: 14px;
    letter-spacing: 1px;
    font-family: 'Conv_FjallaOne-Regular';
    color: #8f9bc9;
    text-transform: uppercase;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 11;
    font-weight: normal;
    user-select: none; 
    transform: perspective(500px);
    text-align: center;
    margin-left: -90px;
    margin-top: -16px; 
    height: 30px;
    width: 180px;
    opacity: 0;
    backdrop-filter: saturate(180%) blur(10px);
    pointer-events: none;
    
    &.is-active {
         pointer-events: auto;
    }

    &::before, 
    &::after {
        content: attr(data-text);
        display: block;
        width: 100%;
        height: 30px;
        line-height: 30px;
        border: 1px solid #8f9bc9;
        position: absolute;
        top: 0;
        left: 0;
        transition: 0.4s all;
        color: #8f9bc9;
        background: #000;

    }

    &::before {
        transform: perspective(500px) rotateX(0) translateY(0);
        transform-origin: 50% 0;
    }

    &::after {
        transform: perspective(500px) rotateX(-90deg) translateY(100%);
        transform-origin: 50% 100%;
        background: none;
        border: 1px solid #8f9bc9;
        color: #8f9bc9;
    }

    &:hover::before {
        transform: perspective(500px) rotateX(90deg) translateY(-100%);
    }

    &:hover::after {
        transform: perspective(500px) rotateX(0) translateY(0);
    } 
 
    
}

    
/* hyperlink */
/*
    
<button class="app-link__button">
    <span>
        <i>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.772 11.347">
                <path d="M6.255 11.117a.778.778 0 000-1.1l-3.578-3.57h18.317a.778.778 0 100-1.556H2.7l3.554-3.554a.784.784 0 10-1.1-1.112L.204 5.182a.7.7 0 000 .988l4.947 4.947a.778.778 0 001.1 0z" fill="#fff"></path>
            </svg>
        </i>
    </span>
    <span class="invert-icon">
        <i>
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 21.772 11.347">
                <path d="M6.255 11.117a.778.778 0 000-1.1l-3.578-3.57h18.317a.778.778 0 100-1.556H2.7l3.554-3.554a.784.784 0 10-1.1-1.112L.204 5.182a.7.7 0 000 .988l4.947 4.947a.778.778 0 001.1 0z" fill="#fff"></path>
            </svg>
        </i>
    </span>
</button> 
*/
.app-link__button {
    display: block;
    position: relative;
    width: 50px;
    height: 21px;
    background: rgba(255, 255, 255, 0);
    border: 1px solid white;
    padding: 5px 0;
    border-radius: 10px;
    overflow: hidden;
    cursor: pointer;
}
    
.app-link__button span, 
.app-link__button svg, 
.app-link__button path {
    pointer-events: none;
    transition: transform 0.25s cubic-bezier(0.19, 1, 0.22, 1);
}
    
.app-link__button svg {
    display: block;
    width: 100%;
    height: 100%;
    transform: rotate(180deg);
}   
    
    
    
.app-link__button span.invert-icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 48px;
    height: 9px;
    top: 5px;
    transform: translate(-100%, 0%);
}
    
.app-link__button span.invert-icon svg path {
    fill: #000;
} 
   
.app-link__button:after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: -1;
    border-radius: 8px;
    transition: transform 0.3s cubic-bezier(0.19, 1, 0.22, 1);
    width: 110%;
    transform: translate(-100%, 0%);
} 
    
.app-link__button:hover:after {
    opacity: 1;
    transform: scale3d(1, 1, 1);
}
   
.app-link__button:hover span:not(.invert-icon) svg {
    transform: translate(100%, 0%) rotate(180deg);
}  
.app-link__button:hover span.invert-icon {
    transform: translate(0%, 0%);
}  

/* 
 ---------------------------
 7. Scrolling Content
 ---------------------------
 */  
    
.app-scrolling-textbox__wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 11;
    width: 620px;
    height: 350px;
    overflow: hidden;
    margin-top: -10px;
    transition: 0.6s all;
    pointer-events: none;
    user-select: none;
    opacity: 0; 
    
    &.scene-ready {
        opacity: 1;
        height: 110px;
        margin-top: 180px;
    } 



    .app-scrolling-textbox {
        position: relative;
        transition: 0.6s all;

        > p {
            display: block;
            font-family: Arial;
            text-transform: uppercase;
            letter-spacing: 1px;
            margin: 0;
            padding: 0;
            width: 100%;
            height: 20px;
            position: absolute;
            font-size: 10px;
            line-height: 2.5;
            text-align: center;
            color: rgba(255,255,255,.18);
            overflow: hidden;
            text-overflow: ellipsis;	
            white-space: nowrap;

        }

    }
    
    &.scene-start .app-scrolling-textbox {
        opacity: 0;
    } 
    
    &.scene-detail {
        margin-left: -400px;
        margin-top: 50px;

        .app-scrolling-textbox {
            opacity: 1;
    
            > p {
                height: 30px;
                line-height: 3.5;
            }
        } 
    }

}

    
/* 
 ---------------------------
 8. Project
 ---------------------------
 */  
.app-project__item {
    width: 100%;
    visibility: hidden;
    margin-bottom: 7rem;
    user-select: none;
    opacity: 0;
    transition: opacity .6s linear, transform 1.3s cubic-bezier(.19,1,.22,1);
    transform: scaleY(2.2) translateY(80px);
    transform-origin: 0 0;
    
    .app-project__image {
        img {
            width: 100%;
            border: 1px solid rgba(128,128,128,.15);
            transform-origin: 0 0;          
        }
    }
    
    &:first-child {
        transition: opacity .6s linear,transform 1.5s cubic-bezier(.19,1,.22,1);
    }

    &.is-active {
        transform: scaleY(1) translateY(0);
        opacity: 1;
    }  
 
    
}

@media all and (max-width:768px) {
    .app-project__item {
        transition: opacity 1s linear,transform 1.6s cubic-bezier(.19,1,.22,1);
    }
}

    
    
