/* 
 ---------------------------
 10. Circle Mask
 ---------------------------
 */  
.app-circle-mask {
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    position: absolute;
    z-index: 0;
    pointer-events: none;
    width: 60vh;
    height: 60vh;
    border-radius: 100%;
    opacity: .7;
    background: rgba(0,0,0,1);
    box-shadow:0px 0px 10px rgba(0,0,0,.7),
              0px 0px 10px rgba(0,0,0,.6), 
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 25px rgba(0,0,0,.6),
              0px 0px 50px rgba(0,0,0,.6),
              0px 0px 50px rgba(0,0,0,.6),
              0px 0px 50px rgba(0,0,0,.7),
              0px 0px 150px rgba(0,0,0,.7),
              0px 10px 100px rgba(0,0,0,.8),
              0px 10px 100px rgba(0,0,0,.8),
              0px 10px 100px rgba(0,0,0,.8),
              0px 10px 100px rgba(0,0,0,.8),
              0px -10px 100px rgba(0,0,0,1),
              0px -10px 100px rgba(0,0,0,1),
              0px 10px 200px rgba(0,0,0,1),
              0px 10px 200px rgba(0,0,0,1),
              0px 0 500px rgba(0,0,0,.7),
              0px 0 500px rgba(0,0,0,.7);    
}  
   
    
    
