/*---- Popwin ----*/
/* Close Button */
.app-close-btn__wrapper--list,
.app-close-btn__wrapper {
    position: fixed;
    z-index: 99995;
    display: none;
    
    right: 1px;
    top: -5px;
    background: rgba(0,0,0,1);
    border-radius: 8px;
    width: 80px;
    height: 80px;
    transform: scale(0.5) translateY(-10px);
    
    &.is-active {
        display: block;
    }
    
    

    .app-close-btn__outer {
        position: relative;
        margin: auto;
        width: 70px;
        cursor: pointer;
        transform: translateY(11px) scale(0.8);


        .app-close-btn__inner {
            width: inherit;
            text-align: center;
			margin-top: -4px;

            &:before, 
            &:after {
                position: absolute;
                content: '';
                height: 6px;
                width: inherit;
                background: #E00638;
                left: 0;
                transition: all .3s ease-in;
            }

            &:before {
                top: 50%;
                transform: rotate(45deg) translate(-5px,-3px);
            }

            &:after {
                bottom: 50%;
                transform: rotate(-45deg);
            } 
        }

        .app-close-btn__label {
            font-size: 1em;
            line-height: 4em;
            text-transform: uppercase;
            color: #fff;
            transition: all .3s ease-in;
            opacity: 0;
            cursor: pointer;
			pointer-events: none;
        }



        &:hover {
            .app-close-btn__label {
                opacity: 1;
            }

            .app-close-btn__inner {
                &:before, 
                &:after {
                    transform: rotate(0);
                }

                &:before {
                    top: 0;
                }

                &:after {
                    bottom: 0;
                }
            }
        }

    }



    
}


.app-close-btn__wrapper--list {
    position: fixed;
    z-index: 99985;
    
}


