        }

        &.focus {
            .app-deco {
                opacity: 1;     
            }
            

        }
    }

    
}
  
.app-deco {
    position: absolute;
    z-index: 0;
    transition: .2s linear;
    transition-delay: .7s;
    opacity: 0;

    &.app-deco-1 {

        top: -100px;
        left: 45%;


       &::after {
            content: '';
            display: block;
            background: #fff;
            height: 70px;
            width: 15px;
            transform: rotate(45deg);
            transform-origin: center center;
            position: absolute;
            top: 50px;
            left: -16px;
            z-index: 0;
        } 


        &::before {
            content: '';
            display: block;
            background: #142feb;
            height: 32px;
            transform: rotate(45deg) translate(45px,67px);
            z-index: 2;
            width: 15px;
            transform-origin: center center;
            position: absolute;
            z-index: 2;
            top: 20px;
            left: -20px; 

        } 

    }

    &.app-deco-2 {
        top: -5px;
        right: -30% !important;
        content: '';
        display: block;
        background: #142feb;
        height: 40px;
        width: 15px;
        transform: rotate(45deg);
        transform-origin: center center;
        position: absolute;
    }


    &.app-deco-3 {
        top: 160px;
        right: -30% !important;
        content: '';
        display: block;
        background: #000a19;
        height: 160px;
        width: 15px;
        transform: rotate(45deg);
        transform-origin: center center;
        position: absolute;
    }


}


@media all and (max-width: 768px) {

    .app-screen-detail #app-screen-detail__about {
        transform: translate(0,50px);
        width: 90vw;
    }

}
      

